userContainer img {\n  width: 90px;\n}\n.appbar {\n  height: 70px;\n  position: relative;\n  border: none !important;\n  display: flex;\n  align-items: center;\n  gap: 10px;\n  padding-left: 10px;\n  padding-right: 10px;\n}\n.appbar .icon {\n  height: 40px;\n}\n.appbar .title h1 {\n  font-size: 24px;\n  font-weight: 200;\n  letter-spacing: 6px;\n  margin: 0;\n}\n.appbar .title h2 {\n  font-size: 14px;\n  font-weight: 400;\n  letter-spacing: 4px;\n  margin: 0;\n}\n.appbar .spacer {\n  flex-grow: 1;\n}\n.appbar .group {\n  display: flex;\n  flex-direction: row;\n  align-items: center;\n}\n.appbar .image-button {\n  cursor: pointer;\n}\n.list {\n  padding: var(--menu-container-padding);\n  margin: 0;\n  border: 0;\n}\n.list .list-item {\n  font-weight: 400;\n  height: 32px;\n  font-size: var(--label-font-size);\n  padding: var(--menu-container-padding);\n  border-radius: 4px;\n}\n.list .list-item:hover {\n  background-color: rgb(var(--list-item-hover-bg-color));\n}\n.list .list-item:hover .list-item-action {\n  opacity: 1;\n  transform: scale(1);\n  transition: all 0.2s ease;\n}\n.list .list-item.selected {\n  background-color: rgb(var(--list-item-selected-bg-color));\n}\n.list .list-item.selected .list-item-action {\n  opacity: 1;\n  transform: scale(1);\n  transition: all 0.2s ease;\n}\n.list .list-item .list-item-action {\n  opacity: 0;\n  transform: scale(0.5);\n  transition: all 0.2s ease;\n  font-size: 20px;\n  min-width: 25px;\n}\n.list .list-item:before {\n  background-color: rgb(var(--tint-color));\n  block-size: 16px;\n  border-radius: 3px;\n  content: \"\";\n  inline-size: 3px;\n  inset-inline-start: 0;\n  opacity: 0;\n  position: absolute;\n  transform: scaleY(0);\n  transition: transform 170ms cubic-bezier(0, 0, 0, 1);\n  margin-left: 2px;\n}\n.list .list-item.selected:before {\n  opacity: 1;\n  transform: scaleY(1);\n}\n.toolbar {\n  position: relative;\n  display: flex;\n  align-items: center;\n  gap: 8px;\n  padding-right: 10px;\n  height: 60px;\n  padd