ors of the left hand side navigation tabbar */\n    --lefttab-font-color: 255, 255, 255;\n    --lefttab-bg-color:  218, 26, 95;\n\n    /* colors for listitems */\n    --menu-container-padding: 4px;\n    --list-item-selected-bg-color: 240, 240, 240;\n    --list-item-hover-bg-color: 248, 248, 248;\n\n    /* form elements */\n    --input-bg-color: 255, 255, 255;\n    --input-color: 0, 0, 0;\n    --input-label-color: 0, 0, 0;\n    --text-color: 0, 0, 0;\n\n    --box-shadow-0: 0 1px 3px rgba(0,0,0,0.12), 0 1px 2px rgba(0,0,0,0.24);\n    --box-shadow-1: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);\n    --box-shadow-2: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);\n    --box-shadow-3: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);\n\n    /* the backgrond-color for a workplace. A workplace is where a docment, content area,... is place.\n    */\n    --workplace-bg-color: 245, 248, 255;\n\n    /* background color of a pane, dialog, document or everything which is place inside a workplaces lke a file-selector */\n    --pane-bg-color: 255, 255, 255;\n\n\n    color: rgb(var(--text-color));\n\n    .electra-button{\n        background-color: rgb(var(--tint-color));\n        color: white;\n        &:hover{\n            filter: brightness(110%);\n        }\n    }\n\n    a {\n        color: rgb(var(--tint-color));\n    }\n\n    /* class used by draw2d if a element is drag&drop from a platette into a workspace */\n    .shadow {\n        filter: drop-shadow(0 1px 3px rgba(0,0,0,0.16));\n    }\n\n    .dropdown-menu {\n        border: 1px solid rgb(var(--border-color));\n        border-radius: @borderRadiusSmall;\n        background-color: rgb(var(--pane-bg-color));\n        li {\n            color: rgb(var(--input-color));\n            &:hover{\n                background-color: rgb(var(--list-item-hover-bg-color));\n            }\n        }\n    }\n\n    input[type=text]{\n        color: rgb(var(--input-color));        \n        border: 1px solid rgb